import React from 'react'
import ReactDOM from 'react-dom'
import {
  Navbar,
  Nav,
  NavItem } from 'react-bootstrap'
import { Link } from 'react-router-dom'

export default class CustomNav extends React.Component{
  render(){
    return (
      <Navbar>
        <Navbar.Header>
          <Navbar.Brand>
            DocMerge
          </Navbar.Brand>
        </Navbar.Header>

        <Nav>

            <NavItem eventKey={1}>
              Home
            </NavItem>

            <NavItem eventKey={2}>
              My Projects
            </NavItem>


        </Nav>

      </Navbar>
    )
  }
}



